@import url('https://fonts.googleapis.com/css2?family=Open+Sans&family=Roboto:wght@300&display=swap');

html, .App {
    height: 100%;
    width: 100%;
}

.App-Frame {
    height: 100vh;
    width: 100vw;
    overflow: hidden;
    position: fixed;
}

#App-Slideshow {
    top: 0;
    height: 100%;
    position: relative;
    transition: top 2s ease;
}

.Slide {
    text-align: center;
    font-size: 2.5em;
    height: 100%;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    transition: all 1.25s ease-in-out;
    font-family: 'Roboto', sans-serif;
}

small 
{
    font-size: .7em;
}

.Slide:not(.active) {
    opacity: 0;
}

#Slide-2.active {
}

.active .jsSlideDown {
    animation: slideDown 1s forwards .5s;
}

.active .jsSlideUp {
    top: 20px;
    animation: slideUp 1s forwards .5s;
}

@keyframes slideDown {
    0% {
        top: -20px;
        opacity: 0;
    }

    100% {
        top: 0;
        opacity: 1;
    }
}

@keyframes slideUp {
    0% {
        top: 20px;
        opacity: 0;
    }

    100% {
        top: 0;
        opacity: 1;
    }
}

@font-face {
    font-family: 'San Francisco Pro';
    src: local('San Francisco Pro Display Ultra Light'), local('San-Francisco-Pro-Display-Ultra-Light'),
        url('fonts/SFProDisplay-Ultralight.woff2') format('woff2'),
        url('fonts/SFProDisplay-Ultralight.woff') format('woff'),
        url('fonts/SFProDisplay-Ultralight.ttf') format('truetype');
    font-weight: 200;
    font-style: normal;
}

.colorize { animation: colorize 0.693s infinite; }

@keyframes colorize
{
    0% { color: red; }
    20% { color: orange; }
    40% { color: goldenrod; }
    60% { color: green; }
    80% { color: blue; }
    100% { color: red; }
}

#Slide-Landing
{
    /* background-image: linear-gradient(180deg, deepskyblue 50%, rgb(36, 200, 255)); */
    background-color: rgb(0, 0, 0);
    color: white;
}

#Slide-Landing span {
    position: relative;
    opacity: 0;
}

#Instructions
{
    color: black;
    font-size: 1em;
    bottom: 0;
    width: 100%;
    display: flex;
    position: fixed;
    text-align: center;
    padding: 3em 0;
    flex-direction: column;
    justify-content: center;
    transition: all 1s ease-in;
    /* background-image: linear-gradient(180deg, rgba(255, 255, 255, 0) 0%, white 35%); */
}

#Instructions.FirstSlide
{
    color: white !important;
}

.Swipe-arrow
{
    top: -.25em;
    position: relative;
    font-size: 3em;
    animation: upAndDown 2s ease-in-out infinite;
    margin-bottom: .25em;
}

@keyframes upAndDown
{
    0% { top: -.25em; }
    50% { top: .25em }
    100% { top: -.25em; }
}

.jsFadeIn
{
    opacity: 0;
    animation: fadeIn 1s ease-in-out 3s forwards;
}

@keyframes fadeIn {
    0% { opacity: 0; }
    100% { opacity: 1; }
}

* {
    user-select: none;;
}

#Contact, #Contact:link, #Contact:visited
{
    text-decoration: none;
    display: inline-block;
    color: white;
    padding: 0.5em;
    background-color: crimson;
    border-radius: 12px;
    font-size: .7em !important;
    position: relative;
}

.active #Contact
{
    /* animation: shake 2s infinite 3s alternate; */
}

@keyframes shake {
    0% { transform: translate(1px, 1px) rotate(0deg); }
    5% { transform: translate(-1px, -2px) rotate(-1deg); }
    10% { transform: translate(-3px, 0px) rotate(1deg); }
    15% { transform: translate(3px, 2px) rotate(0deg); }
    20% { transform: translate(1px, -1px) rotate(1deg); }
    25% { transform: translate(-1px, 2px) rotate(-1deg); }
    30% { transform: translate(0, 0) rotate(0deg); }
    70% { transform: translate(0, 0) rotate(0deg); }
    80% { transform: translate(0, 0) rotate(0deg); }
    90% { transform: translate(0, 0) rotate(0deg); }
    100% { transform: translate(0, 0) rotate(0deg); }
